import {
    init,
    addMessages,
    getLocaleFromNavigator,
    locale,
    _
} from 'svelte-i18n';

import en from '../i18n/en.json';
import es from '../i18n/es.json';
import jp from '../i18n/jp.json';

addMessages('en', en);
addMessages('es', es);
addMessages('jp', jp);

const supportedLocales = ['en', 'es', 'jp'];

function getBestMatchingLocale() {
    if (typeof window !== 'undefined') {
        const savedLocale = localStorage.getItem('userLocale');
        if (savedLocale && supportedLocales.includes(savedLocale)) {
            return savedLocale;
        }
    }
    
    const browserLocale = getLocaleFromNavigator().toLowerCase();
    
    if (supportedLocales.includes(browserLocale)) {
        return browserLocale;
    }
    
    const baseLocale = browserLocale.split('-')[0];
    if (supportedLocales.includes(baseLocale)) {
        return baseLocale;
    }
    
    return 'en';
}

init({
    fallbackLocale: 'en',
    initialLocale: getBestMatchingLocale(),
});

if (typeof window !== 'undefined') {
    locale.subscribe(value => {
        localStorage.setItem('userLocale', value);
    });
}

export {
    _,
    locale
};