<script>
  import { _ } from '../services/i18n';
  import { skills } from '../data/skills';
  import { fade, fly } from 'svelte/transition';
  import { quadInOut } from 'svelte/easing';
  import { onMount } from 'svelte';
  
  // Estado para nodos expandidos
  let expandedNodes = {};
  
  // Inicializar todos los nodos como colapsados
  onMount(() => {
    skills.forEach(skill => {
      expandedNodes[skill.name] = false;
    });
  });
  
  // Alternar nodos
  const toggleNode = (nodeId, event) => {
    event.stopPropagation();
    expandedNodes = {
      ...expandedNodes,
      [nodeId]: !expandedNodes[nodeId]
    };
  };
</script>

<style>
  .skill-tree {
    font-family: 'PressStart2P', 'Courier New', monospace;
    color: var(--gamer-text);
    padding: 20px;
    max-width: 800px;
    margin: 0 auto;
  }
  
  .branch {
    margin-bottom: 10px;
    position: relative;
    padding-left: 20px;
    border-left: 2px solid var(--gamer-primary);
    transition: all 0.3s ease-out;
  }
  
  .branch-title {
    cursor: pointer;
    font-size: 1rem;
    color: var(--gamer-accent);
    margin: 10px 0;
    display: flex;
    align-items: center;
    text-shadow: 0 0 5px var(--gamer-accent);
    transition: all 0.3s ease-out;
    padding: 8px 12px;
    background: rgba(0, 0, 0, 0.2);
    border-radius: 4px;
    user-select: none;
  }
  
  .branch-title:hover {
    color: var(--gamer-primary);
    text-shadow: var(--gamer-glow) var(--gamer-primary);
  }
  
  /* Eliminamos el pseudo-elemento ::before que creaba una flecha adicional */
  .branch-title::before {
    display: none;
  }
  
  .nodes {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(140px, 1fr));
    gap: 10px;
    padding: 10px 0 10px 15px;
    margin: 5px 0 15px 5px;
    border-left: 2px solid var(--gamer-secondary);
    transition: all 0.3s ease-out;
  }
  
  .node {
    background: rgba(0, 0, 0, 0.2);
    border: 1px solid var(--gamer-secondary);
    border-radius: 4px;
    padding: 6px 10px;
    cursor: pointer;
    transition: all 0.2s ease-out;
    font-size: 0.85rem;
    text-align: center;
    overflow: hidden;
  }
  
  .node-content {
    display: block;
    transition: transform 0.2s ease-out;
  }
  
  .node:hover {
    background: rgba(var(--gamer-primary-rgb), 0.1);
    color: var(--gamer-primary);
    border-color: var(--gamer-primary);
    transform: translateY(-2px);
    box-shadow: 0 2px 6px rgba(0, 0, 0, 0.2);
  }
  
  .node:hover .node-content {
    transform: scale(1.05);
  }
  
  .branch-title .arrow {
    display: inline-block;
    width: 16px;
    margin-right: 10px;
    transition: transform 0.2s ease-out;
    font-size: 1em; /* Tamaño más grande para la flecha */
    color: var(--gamer-primary); /* Color más visible */
  }
  
  .branch-title.expanded .arrow {
    transform: rotate(90deg);
  }
  
  .branch-title:hover .arrow {
    color: var(--gamer-accent);
  }
  
  .branch-title:hover {
    background: rgba(var(--gamer-primary-rgb), 0.1);
    color: var(--gamer-primary);
  }
</style>

<div class="skill-tree">
  <h2 class="text-xl font-gamer mb-8 text-center" 
      style="font-family: 'PressStart2P', 'Courier New', monospace !important; 
             color: var(--gamer-primary); 
             text-shadow: var(--gamer-glow) var(--gamer-primary);">
    {$_("skills.title", { default: "Skills" })}
  </h2>
  
  {#each skills as branch}
    <div class="branch">
      <div 
        class="branch-title {expandedNodes[branch.name] ? 'expanded' : ''}" 
        on:click|self={(e) => toggleNode(branch.name, e)}>
        <span class="arrow" aria-hidden="true">
          {expandedNodes[branch.name] ? '▼' : '▶'}
        </span>
        {$_("skills." + branch.name, { default: branch.name })}
      </div>
      
      {#if expandedNodes[branch.name]}
        <div 
          class="nodes"
          in:fly={{
            y: 10,
            duration: 300,
            easing: quadInOut
          }}
          out:fly={{
            y: -10,
            duration: 250,
            easing: quadInOut
          }}
        >
          {#each branch.list as skill, i (skill.name || skill)}
            <div 
              class="node"
              in:fly={{
                y: 10,
                duration: 200,
                delay: i * 30,
                easing: quadInOut
              }}
              out:fly={{
                y: -10,
                duration: 150,
                easing: quadInOut
              }}
            >
              <span class="node-content">
                {skill.name || skill}
              </span>
            </div>
          {/each}
        </div>
      {/if}
    </div>
  {/each}
</div>
